
.banner{
    position: relative;
    width: 100%;
    height: 50vh;
    overflow: hidden;
}

.mouse-container {
    padding-left: 50px;
    align-items: center;
    display: flex;
    position: relative;
    width: 40%;
    height: 20vh;
    background: white;
}
.text{
    align-items: center;
    padding-left: 10px;
    font-size: 60px;
    font-family: "Poppins SemiBold";
}
.banner-container #scene{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.banner-container #scene #text{
    position: absolute;
    display: flex;
    position: relative;
    color: #fff;
    font-size: 14vw;
    z-index: 100;
}

.banner-container #scene #text span{
    position: relative;
    display: inline-block;
    text-shadow: 1px 1px 0 #e1e1e1,
    2px 2px 0 #e1e1e1,
    3px 3px 0 #e1e1e1,
    4px 4px 0 #e1e1e1,
    5px 5px 0 #e1e1e1,
    6px 6px 0 #e1e1e1,
    7px 7px 0 #e1e1e1,
    8px 8px 0 #e1e1e1,
    25px 35px 0 rgba(0,0,0,0.1)
}